import {uniPopup} from "@dcloudio/uni-ui/lib/uni-popup/uni-popup.vue" 
export default {
	data() {
		return {
			background: ['red', 'blue', 'pink'],
			indicatorDots: true,
			indicator1:false,
			autoplay: true,
			interval: 2000,//间隔
			duration: 500,//
			mark:"",
			id:"",
			supplyList:[],
			collection:1,
			shrink:[],
			phone:"",
			branch:0,
			tradeId:"",
		}
	},
	onLoad:function(option){
		var _self = this
		console.log(option)
		_self.mark = option.mark
		_self.id = option.id
		_self.tradeId = option.tradeId
		_self.getInfo()
	},
	onShow:function(){
			var _self = this
		
	},
	methods: {
		apply:function(){
			var _self = this
			 uni.makePhoneCall({
				phoneNumber: _self.phone //仅为示例
			 });
		},
		hidePopup:function(){
			this.showPopupMiddleImg = false
		},
		hidePopup1:function(){
			this.showPopupMiddleImg1 = false
		},
		getInfo:function(){
			var _self = this
			 if(!uni.getStorageSync("openId")||uni.getStorageSync("openId")==undefined||uni.getStorageSync("openId")===""){
				        uni.navigateTo({
				        	url:"../../load/load"
				        })
			}
			uni.request({ 
				url:_self.$api+"dockingManager/declareNewQuery",
				data:{id:_self.id,mark:_self.mark,optionId:uni.getStorageSync("openId"),branch:0,trade:_self.tradeId},
				method:"GET",
				success:function(res){
					console.log(res)
					_self.shrink = res.data[0].shrink.split(',')
					_self.phone = res.data[0].phone
					console.log(_self.shrink)
					_self.supplyList = res.data[0]
					if(res.data[0].state === undefined || res.data[0].state===null){
						_self.collection =1
					}else{
						_self.collection =2
					}
				}
			})
		},
		/**
		 * 收藏
		 */
			collect:function(){
			var _self = this
			if(_self.collection == 1){
				uni.request({
					url:_self.$api+"dockingManager/hideAdd",
					data:{id:_self.id,mark:0,optionId:uni.getStorageSync("openId")},
					success:function(res){
						if(res.data==1){
							console.log("收藏成功")
							_self.collection = 2
						}
						else{
							uni.showToast({
								title:"收藏失败",
								icon:"none"
							})
						} 
					}
				})
			}else{
					uni.request({
					url:_self.$api+"dockingManager/hideDelete",
					data:{id:_self.id,optionId:uni.getStorageSync("openId")},
					success:function(res){
							if(res.data==1){
							console.log("取消收藏成功")
							_self.collection = 1
						}
						else{
							uni.showToast({
								title:"取消收藏失败",
								icon:"none"
							})
						}						
					},
					})
			}
		},
		/**
		 * 转发
		 */  
		onShareAppMessage: function (options) { 
				var _self = this
				if (options.from === 'button') {
				  // 来自页面内转发按钮
				  console.log(options.target)
				}
				return {
				  //## 此为转发页面所显示的标题
				  //title: '好友代付', 
				  //## 此为转发页面的描述性文字
				  desc: _self.supplyList.name, 
				  //## 此为转发给微信好友或微信群后，对方点击后进入的页面链接，可以根据自己的需求添加参数
				  path:  'pages/minePage/myComponyNeedDetail/myComponyNeedDetail?id='+_self.id+"&tradeId="+_self.tradeId+"&mark="+_self.mark, 
				  //## 转发操作成功后的回调函数，用于对发起者的提示语句或其他逻辑处理
				  success: function(res) {
					  uni.request({
					  url:_self.$api+"dockingManager/hideAdd",
					  data:{id:_self.id,mark:1,optionId:uni.getStorageSync("openId")},
					  success:function(res){
						  console.log(res)
						  	console.log("转发成功")
					  },
					  })  
				  },
				  //## 转发操作失败/取消 后的回调处理，一般是个提示语句即可
				  fail: function() {
					  console.log("error")
				  }
				}
		},
	},
	 components: {uniPopup},
}
